<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/wechatFans/list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="send-warp">
      <!-- 微信用户信息 -->
      <div class="wx-userinfo">
        <img :src="userData.headimgurl" alt="" class="wx-userinfo--avatar">
        <div class="wx-userinfo--content">
          <p class="wx-userinfo--name">
            <span v-html="userData.nickname"></span>
          </p>
          <p class="wx-userinfo--info">
            粉丝编号:{{userData.openid}}</p>
          <p class="wx-userinfo--info">关注时间: {{$moment(userData.subscribeTime*1000)}}</p>
          <p class="wx-userinfo--info">{{userData.member?'商城注册会员' : '未注册商城会员'}}</p>
        </div>
      </div>
      <!-- 消息列表 -->
      <h3 class="cms-segmentation">
        消息发送历史记录
      </h3>
      <ul>
        <li v-for="(item,index) in sendItems" :key="index" class="send-list__items">
          <div class="send-list__type">
            <span v-if="item.msgType=='text'">
              【文本消息】
            </span>
            <span v-if="item.msgType=='mpnews'">
              【图文消息】
            </span>
            <span v-if="item.msgType=='mpvideo'">
              【视频消息】
            </span>
            <span v-if="item.msgType=='voice'">
              【语音消息】
            </span>
            <span v-if="item.msgType=='image'">
              【图片消息】
            </span>
          </div>
          <div v-if="item.msgType=='image'||item.msgType=='mpnews'" class="send-list__img">
            <img :src="$cms.weChatUrl(item.mediaJson[0].thumbMediaUrl)" alt="" v-if="item.msgType=='mpnews'">
            <img :src="$cms.weChatUrl(item.mediaJson.url)" alt="" v-if="item.msgType=='image'">
          </div>
          <!-- 标题内容 -->
          <div class="media-preview__inner send-list__inner">
            <div v-if="item.msgType!='text'" class="send-list__title">
              <span v-if="item.msgType=='mpnews'">{{item.mediaJson[0].title}}</span>
              <span :class="[item.msgType=='image'?'':'title-pos']" v-else>{{item.mediaJson.name}}</span>
            </div>
            <div class="media-preview__content">
              <span style="width:300px;display:inline-block;" class="text-overflow" v-if="item.msgType=='mpnews'">{{item.mediaJson[0].digest}}</span>
              <div style="padding-top:10px;" class="text-overflow" v-if="item.msgType=='text'" v-html="showEmojiName(item.mediaJson)"></div>
            </div>
          </div>
          <div class="media-preview__del fr" @click="delSend(item.id)">
            删除
          </div>
          <div class="media-preview__success fr">
            <p >发送成功</p>
            <p class="media-preview__time">{{$moment(item.createTime)}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { getWechatfans, weChatFansSendLog, weChatFansSendDel } from '@/api/wechat/index.js'
import { decodeUnicode, emojiString } from './emoji.js'
export default {
  data() {
    return {
      userData: {},
      sendItems: [],
      loading: false
    }
  },
  methods: {
    delSend(id) { // 删除消息日志列表
      var ids = []
      ids.push(id)
      weChatFansSendDel({ ids: ids }).then(res => {
        if (res.code === 200) {
          this.$successMessage('删除成功')
          this.getFansData(this.$route.query.id)
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    showEmojiName(str) {
      // 处理显示emoji昵称
      let name = decodeUnicode(str)
      name = emojiString(name)
      return name
    },
    getFansData(id) { // 获取消息日志列表
      this.loading = true
      getWechatfans({ id: id }).then(res => {
        this.loading = false
        if (res.code === 200) {
          this.userData = res.data
          this.userData.nickname = this.showEmojiName(res.data.nickname)
          this.getSendIList(this.userData.openid)
        }
      })
    },
    getSendIList(openId) {
      // 获取消息列表记录
      weChatFansSendLog({ openid: openId, page: 1, size: 30 }).then(res => {
        if (res.code === 200) {
          res.data.content.forEach(v => {
            if (v.msgType !== 'text') {
              v.mediaJson = JSON.parse(v.mediaJson)
            }
          })
          this.sendItems = res.data.content
        }
      })
    }
  },
  created() {
    this.getFansData(this.$route.query.id)
  }
}
</script>

<style <style lang="scss" scoped>
.send-list {
  &__items {
    border-bottom: 1px solid #e0e0e0;
    padding: 10px;
    overflow: hidden;
  }
  &__type {
    float: left;
    height: 45px;
    line-height: 45px;
  }
  &__img {
    max-width: 80px;
    height: 45px;
    display: flex;
    justify-content: center;
    float: left;
    img {
      width: auto;
      height: auto;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__inner {
    height: 45px;
  }
  &__title {
    color: #000;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.title-pos{
  display: inline-block;
  padding-top: 10px;
}
</style>



